import Geometry from "./Geometry";
import Matrix4 from "./Matrix4";

class Renderer {
    constructor(param) {

    }

    render(param){
        var that = this;
        param = param || {};
        var geometry = param.geometry || new Geometry();

        // var VSHADER_SOURCE =
        //     'void main() {\n' +
        //     '  gl_Position = vec4(0.0, 0.0, 0.0, 1.0);\n' + // Set the vertex coordinates of the point
        //     '  gl_PointSize = 10.0;\n' +                    // Set the point size
        //     '}\n';
        //
        // var FSHADER_SOURCE =
        //     'void main() {\n' +
        //     '  gl_FragColor = vec4(1.0, 0.0, 0.0, 1.0);\n' + // Set the point color
        //     '}\n';

        var VSHADER_SOURCE =
            'attribute vec4 a_Position;\n' +
            'attribute vec4 a_Color;\n' +
            'uniform mat4 u_MvpMatrix;\n' +
            'varying vec4 v_Color;\n' +
            'attribute vec2 a_TexCoord;\n' +
            'varying vec2 v_TexCoord;\n' +
            'void main() {\n' +
            '  gl_Position = u_MvpMatrix * a_Position;\n' +
            '  v_Color = a_Color;\n' +
            '  v_TexCoord = a_TexCoord;\n' +
            '}\n';

        var FSHADER_SOURCE =
            '#ifdef GL_ES\n' +
            'precision mediump float;\n' +
            '#endif\n' +
            'varying vec4 v_Color;\n' +
            'uniform sampler2D u_Sampler;\n' +
            'varying vec2 v_TexCoord;\n' +
            'void main() {\n' +
            '  gl_FragColor = vec4(1,1,0,1);\n' +
            '}\n';

        var canvas = document.getElementById('webgl');
        var gl = canvas.getContext('webgl');

        var vshader = gl.createShader(gl.VERTEX_SHADER);
        gl.shaderSource(vshader, VSHADER_SOURCE);
        gl.compileShader(vshader);

        var fshader = gl.createShader(gl.FRAGMENT_SHADER);
        gl.shaderSource(fshader, FSHADER_SOURCE);
        gl.compileShader(fshader);

        var program = gl.createProgram();
        gl.attachShader(program, vshader);
        gl.attachShader(program, fshader);

        gl.linkProgram(program);

        gl.useProgram(program);
        gl.program = program;

        var vertexColorBuffer = gl.createBuffer();
        var indexBuffer = gl.createBuffer();

        // Write the vertex coordinates and color to the buffer object
        gl.bindBuffer(gl.ARRAY_BUFFER, vertexColorBuffer);
        gl.bufferData(gl.ARRAY_BUFFER, geometry.vertices, gl.STATIC_DRAW);

        var FSIZE = geometry.vertices.BYTES_PER_ELEMENT;
        // Assign the buffer object to a_Position and enable the assignment
        var a_Position = gl.getAttribLocation(gl.program, 'a_Position');
        gl.vertexAttribPointer(a_Position, 3, gl.FLOAT, false, FSIZE * 8, 0);
        gl.enableVertexAttribArray(a_Position);

        // Get the storage location of a_TexCoord
        var a_TexCoord = gl.getAttribLocation(gl.program, 'a_TexCoord');
        gl.vertexAttribPointer(a_TexCoord, 2, gl.FLOAT, false, FSIZE * 8, FSIZE * 6);
        gl.enableVertexAttribArray(a_TexCoord);  // Enable the assignment of the buffer object

        // Write the indices to the buffer object
        gl.bindBuffer(gl.ELEMENT_ARRAY_BUFFER, indexBuffer);
        gl.bufferData(gl.ELEMENT_ARRAY_BUFFER, geometry.indices, gl.STATIC_DRAW);

        var u_MvpMatrix = gl.getUniformLocation(gl.program, 'u_MvpMatrix');

        // Set the eye point and the viewing volume
        var mvpMatrix = new Matrix4();
        mvpMatrix.setPerspective(30, 1, 1, 100);
        mvpMatrix.lookAt(3, 3, 7, 0, 0, 0, 0, 1, 0);

        // Pass the model view projection matrix to u_MvpMatrix
        gl.uniformMatrix4fv(u_MvpMatrix, false, mvpMatrix.elements);

        gl.clearColor(0.0, 0.0, 0.0, 1.0);
        gl.enable(gl.DEPTH_TEST);

        gl.clear(gl.COLOR_BUFFER_BIT | gl.DEPTH_BUFFER_BIT);

        var texture = gl.createTexture();   // Create a texture object

        // Get the storage location of u_Sampler
        var u_Sampler = gl.getUniformLocation(gl.program, 'u_Sampler');

        var image = new Image();  // Create the image object

        // Register the event handler to be called on loading an image
        image.onload = function(){ that.loadTexture(gl, geometry.indices.length, texture, u_Sampler, image); };
        // Tell the browser to load an image
        image.src = './demo/img/map.png';

        // gl.drawArrays(gl.POINTS, 0, 1);
        // gl.drawElements(gl.TRIANGLES, geometry.indices.length, gl.UNSIGNED_BYTE, 0);


    }

    loadTexture(gl, n, texture, u_Sampler, image){
        gl.pixelStorei(gl.UNPACK_FLIP_Y_WEBGL, 1); // Flip the image's y axis
        // Enable texture unit0
        gl.activeTexture(gl.TEXTURE0);
        // Bind the texture object to the target
        gl.bindTexture(gl.TEXTURE_2D, texture);

        // Set the texture parameters
        gl.texParameteri(gl.TEXTURE_2D, gl.TEXTURE_MIN_FILTER, gl.LINEAR);
        // Set the texture image
        gl.texImage2D(gl.TEXTURE_2D, 0, gl.RGB, gl.RGB, gl.UNSIGNED_BYTE, image);

        // Set the texture unit 0 to the sampler
        gl.uniform1i(u_Sampler, 0);

        gl.clear(gl.COLOR_BUFFER_BIT | gl.DEPTH_BUFFER_BIT);
        gl.drawElements(gl.TRIANGLES, n, gl.UNSIGNED_BYTE, 0);
    }

    toString() {

    }
}

export default Renderer;